.product-container{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  gap:10px;
}
.product{
  padding: 20px;
  border: 2px solid black;
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
  object-fit: cover;
}
.product img{
  height: 13rem;
  width: 13rem;
  object-fit: cover;
}
.button-container{
  margin: 1rem;
  text-align: center;
}
.button-container button{
  padding: 1rem;
  font-weight: bold;
  cursor: pointer;
}